.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  cursor: pointer;
}

.wrapper {
  display: flex;
  align-items: center;
}

.text {
  text-align: center;
}

.dragging {
  box-shadow: 0 0 16px rgba(51, 51, 255, 0.25), 0 0 8px rgba(51, 51, 255, 0.25), 0 4px 32px rgba(51, 51, 255, 0.5);
}
